import idb from 'idb'
const dbVersion = 3
const DB_NAME = 'PROJECTS'

export const saveProjects = async (trArr) => {
    dbPromise.then(db => {
        const tx = db.transaction(DB_NAME, 'readwrite');
        for (const item of trArr) {
            tx.objectStore(DB_NAME).put({
                ...item
            });
        }
        return tx.complete;
    });
}

const dbPromise = idb.open('PERSONEL', dbVersion, upgradeDB => {
    // Note: we don't use 'break' in this switch statement,
    // the fall-through behaviour is what we want.
    switch (upgradeDB.oldVersion) {
        case 0:
            upgradeDB.createObjectStore('keyval');
        case 1:
            upgradeDB.createObjectStore(DB_NAME, { keyPath: '_id' });


        default:
            console.log('default db version is not ok!')
    }
});

const idbKeyval = {
    get(key) {
        return dbPromise.then(db => {
            return db.project('keyval')
                .objectStore('keyval').get(key);
        });
    },
    set(key, val) {
        return dbPromise.then(db => {
            const tx = db.project('keyval', 'readwrite');
            tx.objectStore('keyval').put(val, key);
            return tx.complete;
        });
    },
    delete(key) {
        return dbPromise.then(db => {
            const tx = db.project('keyval', 'readwrite');
            tx.objectStore('keyval').delete(key);
            return tx.complete;
        });
    },
    clear() {
        return dbPromise.then(db => {
            const tx = db.project('keyval', 'readwrite');
            tx.objectStore('keyval').clear();
            return tx.complete;
        });
    },
    keys() {
        return dbPromise.then(db => {
            const tx = db.project('keyval');
            const keys = [];
            const store = tx.objectStore('keyval');

            // This would be store.getAllKeys(), but it isn't supported by Edge or Safari.
            // openKeyCursor isn't supported by Safari, so we fall back
            (store.iterateKeyCursor || store.iterateCursor).call(store, cursor => {
                if (!cursor) return;
                keys.push(cursor.key);
                cursor.continue();
            });

            return tx.complete.then(() => keys);
        });
    }
};


